$blue: #19386e;
$lightblue: #e5ebf5;

@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,400;0,700;1,400;1,700&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,500;1,500&display=swap');

body{
  margin: 0;
  font-family: Roboto,Segoe UI,Myriad,Verdana,sans-serif;
  font-weight: medium;
  color: $blue;
  box-sizing: border-box;
  height: 100vh;
  display:flex;
  flex-direction: column;
}

.row {
  align-items:stretch;
  display: flex;
  flex-direction: row;

  >div:nth-child(1) {
    width: percentage(1 / 9);
    margin-left: 0.75vw;
  }
  >div {
    display: flex;
    align-items: center;
  }

}


.english {
  font-style: italic;
}

#header {
  display: flex;
  background-color: $blue;
  color: white;
  height: 12.5vh;
  font-size: 3vw;

  .english {
    margin-left: percentage(1/18);
  }
}



#departures {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  flex-grow: 1;

  >div:nth-child(odd) {
    background-color: $lightblue;
  }
}

#departures-header {
  height: 4.1666667vh;
  font-style: italic;
  font-weight: normal;
  text-align: left;

  font-size: 1.7vw;
}

.departures-row {
  height: 8.33vh;

  .departure-time{
    font-size: 2.1vw;
  }

  .departure-direction{
    font-size: 2.1vw;
  }

  .departure-via {
    font-size: 1.7vw;
  }

  .departure-extra {
    font-size: 1.9vw;
  }
}


.departure-direction {
  width: percentage(2 / 9);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.departure-via {
  flex-grow: 1;
}

.departure-extra {
  width: percentage(1.5/9);
  display: flex;
  align-items: stretch;
}

