  background: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSI0NSIgaGVpZ2h0PSI0NSI+PGcgZGF0YS1uYW1lPSJMYXllciAyIj48cGF0aCBmaWxsPSIjZmZmIiBzdHJva2U9IiMwMDMwODIiIHN0cm9rZS1taXRlcmxpbWl0PSIxMCIgZD0iTTkuNS41djloLTl2MzVoNDRWLjVoLTM1eiIgZGF0YS1uYW1lPSJMYXllciAxIi8+PC9nPjwvc3ZnPg==") no-repeat 50% 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: stretch;
  background-size: contain;
  width: 7vh;
  margin: 0.6vw 0;
